@font-face {
  font-family: "PT Mono";
  font-display: monospace;
  src: local("PT Mono"), local("PTMono-Regular"), url("../../fonts/ptmono.woff2") format("woff2"), url("../../fonts/ptmono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "PT Mono";
  font-display: monospace;
  src: local("PT Mono Bold"), local("PTMono-Bold"), url("../../fonts/ptmonobold.woff2") format("woff2"), url("../../fonts/ptmonobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  font-display: "Arial";
  src: local("Roboto Condensed"), local("RobotoCondensed-Regular"), url("../../fonts/Robotocondensed.woff2") format("woff2"), url("../../fonts/Robotocondensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  font-display: "Arial";
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../../fonts/Robotocondensedbold.woff2") format("woff2"), url("../../fonts/Robotocondensedbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
